import { Header } from '@/components/Header';
import { getRecipients, type Recipient } from '@/lib/data';
import Link from 'next/link';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { User } from 'lucide-react';

export default function BrowsePage() {
  const recipients = getRecipients();

  return (
    <div className="flex min-h-dvh flex-col">
      <Header />
      <main className="flex-1">
        <div className="container mx-auto max-w-2xl px-4 py-8 md:py-16">
          <div className="space-y-2 text-center">
            <h1 className="font-headline text-3xl font-bold tracking-tighter sm:text-4xl">
              Browse Bottles
            </h1>
            <p className="text-muted-foreground">
              Select a recipient to view their messages.
            </p>
          </div>
          <div className="mt-8 grid grid-cols-2 gap-4 sm:grid-cols-3 md:grid-cols-4">
            {recipients.map((recipient: Recipient) => (
              <Link
                href={`/bottle/${recipient.name}`}
                key={recipient.name}
                className="group"
              >
                <Card className="transform transition-transform duration-200 group-hover:scale-105">
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                      <User className="h-5 w-5" />
                      <span className="capitalize">{recipient.name}</span>
                    </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <p className="text-sm text-muted-foreground">
                      {recipient.messageCount} message
                      {recipient.messageCount > 1 ? 's' : ''}
                    </p>
                  </CardContent>
                </Card>
              </Link>
            ))}
          </div>
        </div>
      </main>
    </div>
  );
}

export function generateMetadata() {
    return {
      title: 'Browse Messages',
    };
  }
